/****************************************************************
  Color & Font Themes
****************************************************************/

/* Kodchasan font loaded via npm install @fontsource/kodchasan */
/* Covered By Your Grace font loaded via npm install @fontsource/covered-by-your-grace */

/* Neon Purple*/
:root {
  --ms1-color-accent: hsl(329.42deg 62.75% 48.43%);
  --ms1-color-accent-gradient: radial-gradient(
    50% 50% at 50% 50%,
    #ff4024 0%,
    #bb2b95 100%
  );
  --ms1-color-accent-light: hsl(329.42deg 62.75% 48.43%);
  /* --ms1-color-accent-dark: hsl(205 78% 22% / 1); */
  --ms1-color-accent-dark: #470d28;
  --ms1-color-background: #000000;
  --ms1-image-background-texture: url("https://www.transparenttextures.com/patterns/brushed-alum.png");
  --ms1-color-background-warm: hsl(33 100% 96% / 1);
  --ms1-color-background-cool: #c7cfd1;
  --ms1-color-background-contrast: #ffffff;
  --ms1--font-heading: normal 700 3.6rem "Covered By Your Grace", sans-serif;
  --ms1--font-heading-2: normal 700 3rem "Covered By Your Grace", sans-serif;
  --ms1-spacing-heading: 0.25em;
  --ms1-spacing-subheading: 0.375em;
  --ms1-spacing-body-text: 0.125em;
  --ms1-font-subheading: normal 700 2rem "Covered By Your Grace", sans-serif;
  --ms1-font-body-text: normal 500 1.6rem "Covered By Your Grace", sans-serif;
}

:root {
  --ms1-navbar-height: 65px;
}

@media screen and (max-width: 900px) {
  :root {
    --ms1--font-heading: normal 700 1.8rem "Covered By Your Grace", sans-serif;
    --ms1--font-heading-2: normal 700 1.6rem "Covered By Your Grace", sans-serif;
    --ms1-spacing-heading: 0.125em;
    --ms1-spacing-subheading: 0.162em;
    --ms1-font-subheading: normal 700 1.4rem "Covered By Your Grace", sans-serif;
    --ms1-font-body-text: normal 500 1.2rem "Covered By Your Grace", sans-serif;
  }
}

@media screen and (max-width: 600px) {
  :root {
    --ms1-navbar-height: 50px;
  }
}

/****************************************************************
  defaults 
****************************************************************/
html {
  font-size: 10px;
  overscroll-behavior: none;
}
* {
  box-sizing: border-box;
}
body {
  background-color: #000000;
  background-color: #000000;
  background-color: var(--ms1-color-background);
  /* background-image: var(--ms1-image-background-texture); */
  background-blend-mode: overlay;
  color: hsl(33 100% 96% / 1);
  color: #bb2b95;
  color: hsl(329.42deg 62.75% 48.43%);
  color: var(--ms1-color-accent);
  font: normal 500 1.6rem "Kodchasan", sans-serif;
  font: normal 500 1.6rem "Covered By Your Grace", sans-serif;
  font: var(--ms1-font-body-text);
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  font: normal 500 1.6rem "Covered By Your Grace", sans-serif;
  font: var(--ms1-font-body-text);
  letter-spacing: 0.125em;
  letter-spacing: var(--ms1-spacing-body-text);
  overscroll-behavior: none;
}
p,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
  background-color: #000000;
  background-color: var(--ms1-color-background);
  background-image: radial-gradient(
    50% 50% at 50% 50%,
    #ff4024 0%,
    #bb2b95 100%
  );
  background-image: var(--ms1-color-accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p {
  word-break: break-word;
}

a {
  color: hsl(329.42deg 62.75% 48.43%);
  color: var(--ms1-color-accent);
}
code {
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace; */
  white-space: pre-wrap;
}

h1 {
  font: normal 700 3.6rem "Covered By Your Grace", sans-serif;
  font: var(--ms1--font-heading);
  letter-spacing: 0.25em;
  letter-spacing: var(--ms1-spacing-heading);
}

h2 {
  font: normal 700 2rem "Covered By Your Grace", sans-serif;
  font: var(--ms1-font-subheading);
  letter-spacing: 0.375em;
  letter-spacing: var(--ms1-spacing-subheading);
}

/* SVG */
#gradient-horizontal {
  --color-stop-1: #ff4024;
  --color-stop-2: #bb2b95;
  --color-stop-3: #bb2b95;
}

#radial-gradient {
  --color-middle: #ff4024;
  --color-fill: #bb2b95;
  --color-edge: #bb2b95;
}
/******/
.section-title {
  font: normal 700 3rem "Covered By Your Grace", sans-serif;
  font: var(--ms1--font-heading-2);
  letter-spacing: 0.25em;
  letter-spacing: var(--ms1-spacing-heading);
  word-wrap: break-word;
}

